"use client";

import { useState, useRef, useCallback } from "react";

type Screenshot = {
  id: string;
  image: string;
  emotion: string;
  timestamp: number;
};

type JudgeResult = {
  transcript: string;
  emotion: string;
  score: number;
  screenshots: Screenshot[];
};

export default function Home() {
  const [isRecording, setIsRecording] = useState(false);
  const [result, setResult] = useState<JudgeResult | null>(null);
  const [activeTab, setActiveTab] = useState<"score" | "screenshots" | "transcript">("score");
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const chunksRef = useRef<Blob[]>([]);
  const screenshotsRef = useRef<Screenshot[]>([]);
  const captureIntervalRef = useRef<NodeJS.Timeout | null>(null);

  const captureScreenshot = useCallback(() => {
    if (!videoRef.current || !canvasRef.current) return;

    const video = videoRef.current;
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");

    if (!ctx) return;

    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;
    ctx.drawImage(video, 0, 0);

    const image = canvas.toDataURL("image/jpeg", 0.8);

    const emotions = ["Confused", "Skeptical", "Impressed", "Bored", "Surprised", "Amused"];
    const randomEmotion = emotions[Math.floor(Math.random() * emotions.length)];

    const screenshot: Screenshot = {
      id: Date.now().toString(),
      image,
      emotion: randomEmotion,
      timestamp: Date.now(),
    };

    screenshotsRef.current.push(screenshot);
  }, []);

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true,
      });

      if (videoRef.current) {
        videoRef.current.srcObject = stream;
      }

      const mediaRecorder = new MediaRecorder(stream);
      mediaRecorderRef.current = mediaRecorder;
      chunksRef.current = [];
      screenshotsRef.current = [];

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunksRef.current.push(event.data);
        }
      };

      mediaRecorder.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: "video/webm" });
        console.log("Recording stopped, blob size:", blob.size);

        setResult({
          transcript: `Judge: "So tell me about your project..."

You: "We built an app that uses AI to analyze hackathon judges."

Judge: "That's... interesting. How does it work exactly?"

You: "It records video and audio, then uses computer vision and speech-to-text to analyze your reactions and feedback."

Judge: "Wait, are you recording me right now?"

You: "...maybe."

Judge: "I'm not sure how I feel about this."`,
          emotion: "Skeptical",
          score: 6,
          screenshots: screenshotsRef.current,
        });
      };

      captureIntervalRef.current = setInterval(captureScreenshot, 3000);

      mediaRecorder.start();
      setIsRecording(true);
      setResult(null);
      setActiveTab("score");
    } catch (err) {
      console.error("Error accessing camera/microphone:", err);
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);

      if (captureIntervalRef.current) {
        clearInterval(captureIntervalRef.current);
        captureIntervalRef.current = null;
      }

      const stream = videoRef.current?.srcObject as MediaStream;
      stream?.getTracks().forEach((track) => track.stop());
      if (videoRef.current) {
        videoRef.current.srcObject = null;
      }
    }
  };

  const getScoreColor = (score: number) => {
    if (score >= 7) return "text-green-400";
    if (score >= 4) return "text-yellow-400";
    return "text-red-400";
  };

  return (
    <div className="min-h-screen text-white dirt-bg">
      <style jsx global>{`
        @import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

        .minecraft-font {
          font-family: 'VT323', monospace;
          letter-spacing: 1px;
        }

        /* Dirt background texture */
        .dirt-bg {
          background-color: #8B5A2B;
          background-image:
            repeating-linear-gradient(
              0deg,
              transparent,
              transparent 8px,
              rgba(0,0,0,0.1) 8px,
              rgba(0,0,0,0.1) 16px
            ),
            repeating-linear-gradient(
              90deg,
              transparent,
              transparent 8px,
              rgba(0,0,0,0.1) 8px,
              rgba(0,0,0,0.1) 16px
            ),
            linear-gradient(
              180deg,
              #6B4423 0%,
              #8B5A2B 20%,
              #7A4E28 40%,
              #8B5A2B 60%,
              #6B4423 80%,
              #8B5A2B 100%
            );
          background-size: 16px 16px, 16px 16px, 64px 64px;
        }

        /* Netherrack texture for transcript */
        .netherrack-bg {
          background-color: #723232;
          background-image:
            repeating-linear-gradient(
              0deg,
              transparent,
              transparent 4px,
              rgba(0,0,0,0.15) 4px,
              rgba(0,0,0,0.15) 8px
            ),
            repeating-linear-gradient(
              90deg,
              transparent,
              transparent 4px,
              rgba(80,20,20,0.3) 4px,
              rgba(80,20,20,0.3) 8px
            ),
            linear-gradient(
              135deg,
              #5c2828 0%,
              #723232 25%,
              #8a3a3a 50%,
              #723232 75%,
              #5c2828 100%
            );
          background-size: 8px 8px, 8px 8px, 32px 32px;
        }

        /* End Stone texture for screenshots */
        .endstone-bg {
          background-color: #DDDFA5;
          background-image:
            repeating-linear-gradient(
              0deg,
              transparent,
              transparent 6px,
              rgba(0,0,0,0.05) 6px,
              rgba(0,0,0,0.05) 12px
            ),
            repeating-linear-gradient(
              90deg,
              transparent,
              transparent 6px,
              rgba(180,180,120,0.3) 6px,
              rgba(180,180,120,0.3) 12px
            ),
            linear-gradient(
              45deg,
              #c9cb8d 0%,
              #DDDFA5 30%,
              #e8eab8 50%,
              #DDDFA5 70%,
              #c9cb8d 100%
            );
          background-size: 12px 12px, 12px 12px, 48px 48px;
        }

        /* Stone texture for score */
        .stone-bg {
          background-color: #7F7F7F;
          background-image:
            repeating-linear-gradient(
              0deg,
              transparent,
              transparent 8px,
              rgba(0,0,0,0.1) 8px,
              rgba(0,0,0,0.1) 16px
            ),
            repeating-linear-gradient(
              90deg,
              transparent,
              transparent 8px,
              rgba(100,100,100,0.2) 8px,
              rgba(100,100,100,0.2) 16px
            ),
            linear-gradient(
              180deg,
              #6a6a6a 0%,
              #7F7F7F 25%,
              #8a8a8a 50%,
              #7F7F7F 75%,
              #6a6a6a 100%
            );
          background-size: 16px 16px, 16px 16px, 64px 64px;
        }

        .minecraft-box {
          border: 4px solid #1a1a1a;
          box-shadow:
            inset -3px -3px 0 rgba(0,0,0,0.4),
            inset 3px 3px 0 rgba(255,255,255,0.15),
            4px 4px 0 rgba(0,0,0,0.3);
        }

        .minecraft-btn {
          border: 4px solid #1a1a1a;
          box-shadow:
            inset -3px -3px 0 rgba(0,0,0,0.4),
            inset 3px 3px 0 rgba(255,255,255,0.2),
            3px 3px 0 rgba(0,0,0,0.3);
          image-rendering: pixelated;
        }

        .minecraft-btn:active {
          box-shadow:
            inset 3px 3px 0 rgba(0,0,0,0.4),
            inset -3px -3px 0 rgba(255,255,255,0.1);
          transform: translate(2px, 2px);
        }

        .minecraft-btn-green {
          background: linear-gradient(180deg, #5a5 0%, #383 100%);
        }

        .minecraft-btn-red {
          background: linear-gradient(180deg, #c55 0%, #833 100%);
        }

        .tab-btn {
          border: 3px solid #1a1a1a;
          box-shadow:
            inset -2px -2px 0 rgba(0,0,0,0.3),
            inset 2px 2px 0 rgba(255,255,255,0.1);
        }

        .tab-btn.active {
          box-shadow:
            inset 2px 2px 0 rgba(0,0,0,0.3),
            inset -2px -2px 0 rgba(255,255,255,0.1);
        }
      `}</style>

      <div className="mx-auto max-w-2xl px-6 py-12">
        <canvas ref={canvasRef} className="hidden" />

        {/* Header */}
        <div className="mb-10 text-center">
          <h1
            className="minecraft-font text-6xl font-bold tracking-wider"
            style={{
              textShadow: "4px 4px 0 #3d2817, -2px -2px 0 rgba(255,255,255,0.2)",
              color: "#f0e68c"
            }}
          >
            UNO REVERSE
          </h1>
          <p
            className="minecraft-font mt-3 text-2xl"
            style={{
              textShadow: "2px 2px 0 #3d2817",
              color: "#d4c896"
            }}
          >
            [ Judge the Judges ]
          </p>
        </div>

        {/* Video Preview */}
        <div className="minecraft-box mb-8 bg-black p-1">
          <video
            ref={videoRef}
            autoPlay
            muted
            playsInline
            className="aspect-video w-full bg-neutral-900"
          />
        </div>

        {/* Record Button */}
        <div className="mb-10 flex justify-center">
          {!isRecording ? (
            <button
              onClick={startRecording}
              className="minecraft-btn minecraft-btn-green minecraft-font flex items-center gap-3 px-10 py-4 text-2xl text-white transition-all hover:brightness-110"
            >
              <span
                className="inline-block h-4 w-4 bg-red-500"
                style={{ boxShadow: "inset -2px -2px 0 #800, inset 2px 2px 0 #f88" }}
              />
              START RECORDING
            </button>
          ) : (
            <button
              onClick={stopRecording}
              className="minecraft-btn minecraft-btn-red minecraft-font flex items-center gap-3 px-10 py-4 text-2xl text-white transition-all hover:brightness-110"
            >
              <span
                className="inline-block h-4 w-4 bg-white"
                style={{ boxShadow: "inset -2px -2px 0 #888, inset 2px 2px 0 #fff" }}
              />
              STOP RECORDING
            </button>
          )}
        </div>

        {/* Results */}
        {result && (
          <div className="minecraft-box overflow-hidden stone-bg">
            {/* Tabs */}
            <div className="flex">
              <button
                onClick={() => setActiveTab("score")}
                className={`tab-btn minecraft-font flex-1 py-3 text-lg transition-colors stone-bg ${
                  activeTab === "score" ? "active brightness-110" : "brightness-75 hover:brightness-90"
                }`}
                style={{
                  color: activeTab === "score" ? "#fff" : "#aaa",
                  textShadow: "2px 2px 0 #333"
                }}
              >
                SCORE
              </button>
              <button
                onClick={() => setActiveTab("screenshots")}
                className={`tab-btn minecraft-font flex-1 py-3 text-lg transition-colors endstone-bg ${
                  activeTab === "screenshots" ? "active brightness-110" : "brightness-75 hover:brightness-90"
                }`}
                style={{
                  color: activeTab === "screenshots" ? "#4a4a2a" : "#7a7a5a",
                  textShadow: "1px 1px 0 rgba(255,255,255,0.5)"
                }}
              >
                FACES ({result.screenshots.length})
              </button>
              <button
                onClick={() => setActiveTab("transcript")}
                className={`tab-btn minecraft-font flex-1 py-3 text-lg transition-colors netherrack-bg ${
                  activeTab === "transcript" ? "active brightness-110" : "brightness-75 hover:brightness-90"
                }`}
                style={{
                  color: activeTab === "transcript" ? "#ffcccc" : "#cc9999",
                  textShadow: "2px 2px 0 #2a1010"
                }}
              >
                TRANSCRIPT
              </button>
            </div>

            {/* Tab Content */}
            <div>
              {/* Score Tab - Stone theme */}
              {activeTab === "score" && (
                <div className="stone-bg p-6 space-y-6 text-center">
                  <div>
                    <p
                      className="minecraft-font mb-2 text-xl"
                      style={{ color: "#ccc", textShadow: "2px 2px 0 #333" }}
                    >
                      JUDGE SCORE
                    </p>
                    <p
                      className={`minecraft-font text-9xl font-bold ${getScoreColor(result.score)}`}
                      style={{ textShadow: "6px 6px 0 #333" }}
                    >
                      {result.score}
                      <span className="text-5xl" style={{ color: "#999" }}>/10</span>
                    </p>
                  </div>
                  <div
                    className="pt-6"
                    style={{ borderTop: "4px solid #555" }}
                  >
                    <p
                      className="minecraft-font mb-2 text-xl"
                      style={{ color: "#ccc", textShadow: "2px 2px 0 #333" }}
                    >
                      OVERALL VIBE
                    </p>
                    <p
                      className="minecraft-font text-4xl"
                      style={{ color: "#fff", textShadow: "3px 3px 0 #333" }}
                    >
                      {result.emotion}
                    </p>
                  </div>
                </div>
              )}

              {/* Screenshots Tab - End Stone theme */}
              {activeTab === "screenshots" && (
                <div className="endstone-bg p-6">
                  {result.screenshots.length === 0 ? (
                    <p
                      className="minecraft-font py-8 text-center text-xl"
                      style={{ color: "#5a5a3a", textShadow: "1px 1px 0 rgba(255,255,255,0.5)" }}
                    >
                      No screenshots yet. Record longer!
                    </p>
                  ) : (
                    <div className="grid grid-cols-2 gap-4">
                      {result.screenshots.map((screenshot) => (
                        <div
                          key={screenshot.id}
                          className="minecraft-box overflow-hidden"
                          style={{ backgroundColor: "#b8ba7a" }}
                        >
                          <img
                            src={screenshot.image}
                            alt={`Screenshot - ${screenshot.emotion}`}
                            className="aspect-video w-full object-cover"
                          />
                          <div className="p-2 text-center endstone-bg">
                            <p
                              className="minecraft-font text-lg"
                              style={{ color: "#4a4a2a", textShadow: "1px 1px 0 rgba(255,255,255,0.5)" }}
                            >
                              {screenshot.emotion}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              )}

              {/* Transcript Tab - Netherrack theme */}
              {activeTab === "transcript" && (
                <div className="netherrack-bg p-6">
                  <div
                    className="minecraft-box p-4"
                    style={{
                      backgroundColor: "rgba(60,20,20,0.5)",
                      maxHeight: "400px",
                      overflowY: "auto"
                    }}
                  >
                    <p
                      className="minecraft-font whitespace-pre-wrap text-lg leading-relaxed"
                      style={{ color: "#ffcccc", textShadow: "2px 2px 0 #2a1010" }}
                    >
                      {result.transcript}
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}

        {/* Decorative Minecraft blocks */}
        <div className="mt-12 flex justify-center gap-1">
          {[
            { bg: "#567d46", name: "grass" },
            { bg: "#8B5A2B", name: "dirt" },
            { bg: "#7F7F7F", name: "stone" },
            { bg: "#723232", name: "netherrack" },
            { bg: "#DDDFA5", name: "endstone" },
          ].map((block, i) => (
            <div
              key={i}
              className="h-8 w-8"
              title={block.name}
              style={{
                backgroundColor: block.bg,
                boxShadow: "inset -3px -3px 0 rgba(0,0,0,0.3), inset 3px 3px 0 rgba(255,255,255,0.2)",
                border: "2px solid #1a1a1a"
              }}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
